import { defineStore } from 'pinia'
import { upload } from '@/utils/request.js'
import { API_ENDPOINTS } from '@/utils/constants.js'

export const useUploadStore = defineStore('upload', {
  state: () => ({
    // ÂΩìÂâçÊñá‰ª∂Á±ªÂûã (0: Áü•ËØÜÁÇπ, 1: È¢òÂ∫ì)
    currentFileType: 0,
    
    // Êñá‰ª∂ÂàóË°®
    filesList: [],
    
    // ‰∏ä‰º†Áä∂ÊÄÅ
    uploadStatus: {
      uploading: false,
      progress: {
        show: false,
        current: 0,
        total: 0,
        percent: 0
      }
    },
    
    // ‰∏ä‰º†ÂéÜÂè≤ËÆ∞ÂΩï
    uploadHistory: [
      {
        id: 1,
        fileName: 'È°πÁõÆÁÆ°ÁêÜÂü∫Á°ÄÁü•ËØÜ.docx',
        fileType: 'Áü•ËØÜÁÇπ',
        uploadTime: '2024-01-15 14:30',
        status: 'ÊàêÂäü',
        fileSize: '2.5MB',
        recordCount: 156
      },
      {
        id: 2,
        fileName: 'Ê®°ÊãüËÄÉËØïÈ¢òÂ∫ì.xlsx',
        fileType: 'È¢òÂ∫ì',
        uploadTime: '2024-01-14 16:45',
        status: 'Â§±Ë¥•',
        fileSize: '1.8MB',
        errorMessage: 'Êñá‰ª∂Ê†ºÂºè‰∏çÊ≠£Á°Æ'
      }
    ],
    
    // ÈÖçÁΩÆ‰ø°ÊÅØ
    config: {
      // Êñá‰ª∂Á±ªÂûãÈÄâÈ°π
      fileTypeOptions: ['Áü•ËØÜÁÇπ', 'È¢òÂ∫ì'],
      
      // ÂÖÅËÆ∏ÁöÑÊñá‰ª∂Êâ©Â±ïÂêç
      allowedExtensions: {
        0: ['doc', 'docx', 'pdf', 'txt', 'md'], // Áü•ËØÜÁÇπ
        1: ['xlsx', 'xls', 'csv', 'json']       // È¢òÂ∫ì
      },
      
      // Êñá‰ª∂Â§ßÂ∞èÈôêÂà∂ (MB)
      maxFileSize: 10,
      
      // ÊúÄÂ§ßÊñá‰ª∂Êï∞Èáè
      maxFileCount: 5,
      
      // Êñá‰ª∂Á±ªÂûãÊèèËø∞
      typeDescriptions: {
        0: '‰∏ä‰º†Áü•ËØÜÁÇπÊñáÊ°£ÔºåÁ≥ªÁªüÂ∞ÜËá™Âä®Ëß£ÊûêÂπ∂ÂàÜÁ±ªÂ≠òÂÇ®Â≠¶‰π†ÂÜÖÂÆπ',
        1: '‰∏ä‰º†È¢òÂ∫ìÊñá‰ª∂ÔºåÁ≥ªÁªüÂ∞ÜËß£ÊûêÈ¢òÁõÆ„ÄÅÈÄâÈ°πÂíåÁ≠îÊ°à‰ø°ÊÅØ'
      }
    }
  }),
  
  getters: {
    // Ëé∑ÂèñÂΩìÂâçÊñá‰ª∂Á±ªÂûãÊñáÊú¨
    currentFileTypeText: (state) => {
      return state.config.fileTypeOptions[state.currentFileType]
    },
    
    // Ëé∑ÂèñÂΩìÂâçÂÖÅËÆ∏ÁöÑÊñá‰ª∂Êâ©Â±ïÂêç
    currentAllowedExtensions: (state) => {
      return state.config.allowedExtensions[state.currentFileType]
    },
    
    // Ëé∑ÂèñÂΩìÂâçÊñá‰ª∂Á±ªÂûãÊèèËø∞
    currentTypeDescription: (state) => {
      return state.config.typeDescriptions[state.currentFileType]
    },
    
    // Ëé∑Âèñ‰∏ä‰º†ËøõÂ∫¶ÁôæÂàÜÊØî
    uploadProgressPercent: (state) => {
      const { current, total } = state.uploadStatus.progress
      return total > 0 ? Math.round((current / total) * 100) : 0
    },
    
    // Ëé∑ÂèñÊàêÂäü‰∏ä‰º†ÁöÑËÆ∞ÂΩïÊï∞Èáè
    successUploadCount: (state) => {
      return state.uploadHistory.filter(record => record.status === 'ÊàêÂäü').length
    },
    
    // Ëé∑ÂèñÂ§±Ë¥•‰∏ä‰º†ÁöÑËÆ∞ÂΩïÊï∞Èáè
    failedUploadCount: (state) => {
      return state.uploadHistory.filter(record => record.status === 'Â§±Ë¥•').length
    }
  },
  
  actions: {
    // ËÆæÁΩÆÊñá‰ª∂Á±ªÂûã
    setFileType(typeIndex) {
      this.currentFileType = typeIndex
      // ÂàáÊç¢Á±ªÂûãÊó∂Ê∏ÖÁ©∫Êñá‰ª∂ÂàóË°®
      this.clearFiles()
    },
    
    // Ê∑ªÂä†Êñá‰ª∂Âà∞ÂàóË°®
    addFiles(files) {
      // È™åËØÅÊñá‰ª∂Êï∞Èáè
      const totalCount = this.filesList.length + files.length
      if (totalCount > this.config.maxFileCount) {
        const message = `ÊúÄÂ§öÂè™ËÉΩÈÄâÊã©${this.config.maxFileCount}‰∏™Êñá‰ª∂`
        uni.showToast({ title: message, icon: 'none' })
        return false
      }
      
      // È™åËØÅÊñá‰ª∂Á±ªÂûãÂíåÂ§ßÂ∞è
      const validFiles = []
      for (const file of files) {
        if (this.validateFile(file)) {
          validFiles.push({
            ...file,
            id: Date.now() + Math.random(),
            uploadStatus: 'pending'
          })
        }
      }
      
      this.filesList.push(...validFiles)
      return validFiles.length > 0
    },
    
    // È™åËØÅÂçï‰∏™Êñá‰ª∂
    validateFile(file) {
      // È™åËØÅÊñá‰ª∂Êâ©Â±ïÂêç
      const extension = this.getFileExtension(file.name)
      if (!this.currentAllowedExtensions.includes(extension)) {
        uni.showToast({
          title: `‰∏çÊîØÊåÅ${extension}Ê†ºÂºèÊñá‰ª∂`,
          icon: 'none'
        })
        return false
      }
      
      // È™åËØÅÊñá‰ª∂Â§ßÂ∞è
      const fileSizeMB = file.size / (1024 * 1024)
      if (fileSizeMB > this.config.maxFileSize) {
        uni.showToast({
          title: `Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá${this.config.maxFileSize}MB`,
          icon: 'none'
        })
        return false
      }
      
      return true
    },
    
    // Ëé∑ÂèñÊñá‰ª∂Êâ©Â±ïÂêç
    getFileExtension(fileName) {
      return fileName.split('.').pop().toLowerCase()
    },
    
    // Ê∏ÖÁ©∫Êñá‰ª∂ÂàóË°®
    clearFiles() {
      this.filesList = []
    },
    
    // ÁßªÈô§Âçï‰∏™Êñá‰ª∂
    removeFile(fileId) {
      const index = this.filesList.findIndex(file => file.id === fileId)
      if (index > -1) {
        this.filesList.splice(index, 1)
      }
    },
    
    // ÂºÄÂßã‰∏ä‰º†
    async startUpload() {
      if (this.filesList.length === 0) {
        uni.showToast({ title: 'ËØ∑ÂÖàÈÄâÊã©Êñá‰ª∂', icon: 'none' })
        return false
      }
      
      if (this.uploadStatus.uploading) {
        uni.showToast({ title: 'Ê≠£Âú®‰∏ä‰º†‰∏≠...', icon: 'none' })
        return false
      }
      
      this.uploadStatus.uploading = true
      this.uploadStatus.progress = {
        show: true,
        current: 0,
        total: this.filesList.length,
        percent: 0
      }
      
      try {
        await this.processUpload()
        uni.showToast({ title: '‰∏ä‰º†ÂÆåÊàê', icon: 'success' })
      } catch (error) {
        console.error('‰∏ä‰º†ËøáÁ®ãÂá∫Èîô:', error)
        uni.showToast({ title: '‰∏ä‰º†ËøáÁ®ãÂá∫Èîô', icon: 'error' })
      } finally {
        this.uploadStatus.uploading = false
        this.uploadStatus.progress.show = false
      }
    },
    
    // Â§ÑÁêÜ‰∏ä‰º†ËøáÁ®ã
    async processUpload() {
      for (let i = 0; i < this.filesList.length; i++) {
        const file = this.filesList[i]
        this.uploadStatus.progress.current = i + 1
        this.uploadStatus.progress.percent = Math.round(((i + 1) / this.filesList.length) * 100)
        
        try {
          const result = await this.uploadSingleFile(file)
          this.addToHistory({
            fileName: file.name,
            fileType: this.currentFileTypeText,
            uploadTime: new Date().toLocaleString('zh-CN'),
            status: 'ÊàêÂäü',
            fileSize: this.formatFileSize(file.size),
            recordCount: result.recordCount || 0
          })
        } catch (error) {
          this.addToHistory({
            fileName: file.name,
            fileType: this.currentFileTypeText,
            uploadTime: new Date().toLocaleString('zh-CN'),
            status: 'Â§±Ë¥•',
            fileSize: this.formatFileSize(file.size),
            errorMessage: error.message || '‰∏ä‰º†Â§±Ë¥•'
          })
        }
        
        // Ê®°Êãü‰∏ä‰º†Âª∂Ëøü
        await new Promise(resolve => setTimeout(resolve, 1000))
      }
      
      // ‰∏ä‰º†ÂÆåÊàêÂêéÊ∏ÖÁ©∫Êñá‰ª∂ÂàóË°®
      this.clearFiles()
    },
    
    // ‰∏ä‰º†Âçï‰∏™Êñá‰ª∂ (Ë∞ÉÁî®ÁúüÂÆûAPI)
    async uploadSingleFile(file) {
      try {
        console.log('üì§ ÂºÄÂßã‰∏ä‰º†Êñá‰ª∂:', file.name)
        console.log('üîó ‰∏ä‰º†APIÁ´ØÁÇπ:', API_ENDPOINTS.FILES.UPLOAD)
        
        // ÂáÜÂ§áË°®ÂçïÊï∞ÊçÆ
        const formData = {
          name: file.name,
          description: `${this.currentFileTypeText}Êñá‰ª∂`,
          type: this.currentFileType
        }
        
        console.log('üìã Ë°®ÂçïÊï∞ÊçÆ:', formData)
        
        // Ë∞ÉÁî®‰∏ä‰º†API
        const result = await upload(
          API_ENDPOINTS.FILES.UPLOAD,
          file.path || file.url,
          formData
        )
        
        console.log('‚úÖ Êñá‰ª∂‰∏ä‰º†ÊàêÂäü:', result)
        
        return {
          success: true,
          recordCount: result.parsed_questions || 0,
          fileId: result.id
        }
      } catch (error) {
        console.error('‚ùå Êñá‰ª∂‰∏ä‰º†Â§±Ë¥•:', error)
        throw new Error(error.message || 'Êñá‰ª∂‰∏ä‰º†Â§±Ë¥•')
      }
    },
    
    // Ê∑ªÂä†Âà∞‰∏ä‰º†ÂéÜÂè≤
    addToHistory(record) {
      const newRecord = {
        ...record,
        id: Date.now() + Math.random()
      }
      this.uploadHistory.unshift(newRecord)
      
      // ÈôêÂà∂ÂéÜÂè≤ËÆ∞ÂΩïÊï∞Èáè
      if (this.uploadHistory.length > 50) {
        this.uploadHistory.pop()
      }
    },
    
    // Ê∏ÖÁ©∫‰∏ä‰º†ÂéÜÂè≤
    clearHistory() {
      this.uploadHistory = []
    },
    
    // Âà†Èô§ÂçïÊù°ÂéÜÂè≤ËÆ∞ÂΩï
    deleteHistoryRecord(recordId) {
      const index = this.uploadHistory.findIndex(record => record.id === recordId)
      if (index > -1) {
        this.uploadHistory.splice(index, 1)
      }
    },
    
    // Ê†ºÂºèÂåñÊñá‰ª∂Â§ßÂ∞è
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B'
      const k = 1024
      const sizes = ['B', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÂõæÊ†á
    getStatusIcon(status) {
      const icons = {
        'ÊàêÂäü': 'checkmarkempty',
        'Â§±Ë¥•': 'closeempty',
        'Â§ÑÁêÜ‰∏≠': 'spinner-cycle'
      }
      return icons[status] || 'help'
    },
    
    // Ëé∑ÂèñÁä∂ÊÄÅÈ¢úËâ≤
    getStatusColor(status) {
      const colors = {
        'ÊàêÂäü': '#28a745',
        'Â§±Ë¥•': '#dc3545',
        'Â§ÑÁêÜ‰∏≠': '#007AFF'
      }
      return colors[status] || '#666'
    }
  }
})
